.card {
  // transform-style: preserve-3d;
  // transition: transform 1s;
  position: relative;
}

.card img {
  width: 100%;
  height: 100%;
  display: block;
  border: 2px solid #fff;
  border-radius: 0.25rem;
  box-shadow: rgba(255, 255, 255, 0.2) 0px 5px 1rem;
}

.card .front {
  position: absolute;
  transform: rotateY(90deg);
  transition: all ease-in 0.2s;
}

.flipped .front {
  transform: rotateY(0deg);
  transition-delay: 0.2s;
}

.card .back {
  transition: all ease-in 0.2s;
  transition-delay: 0.2s;
}

.flipped .back {
  transform: rotateY(90deg);
  transition-delay: 0s;
}
.front,
.back {
  //   position: absolute;
  backface-visibility: hidden;
